const showMoreBtn = document.querySelector('.rating__show-more');
const ratingContent = document.querySelectorAll('.rating__card');
const ratingContentLength = document.querySelectorAll('.rating__card').length;

let counter = 8;

if(showMoreBtn) {
  showMoreBtn.addEventListener('click', () => {
    counter += 4;

    const ratingArray = Array.from(document.querySelector('.rating__content').children)
    const newRatingContent = ratingArray.slice(0, counter);

    newRatingContent.forEach(el => {
      el.classList.add('is-visible');
    });

    if (newRatingContent.length === ratingContentLength) {
      showMoreBtn.style.display = 'none';
    }
  });

};
