const searchInput = document.querySelector('#search__input');
const searchIc = document.querySelector('#search-ic')
const searcBtn = document.querySelector('.search__btn')

searchInput.addEventListener('input', () => {
  searchInput.classList.add('not-focus');
  searchIc.classList.add('is-focus');
})

searchInput.addEventListener('change', () => {
  searchInput.classList.remove('not-focus');
  searchIc.classList.remove('is-focus');
})

searcBtn.addEventListener('click', () => {
  searchInput.classList.remove('not-focus');
  searchIc.classList.remove('is-focus');
  searchInput.value  = '';
})


//////////////////////////
const formchInput = document.querySelectorAll('.form__input');
formchInput.forEach(el => {
  el.addEventListener('input', () => {
    el.classList.add('input--active');
  });

  el.addEventListener('change', () => {
    el.classList.remove('input--active');
  })
})

